<script>
import ModalJoinVote from "@/components/ModalJoinVote";
import ModalNodeDetail from "@/components/ModalNodeDetail";
import ModalRule from "@/components/ModalRule";
import Loading from "@/components/Loading";
import moment from "moment";
import { download } from "@/utils/tools";

import { mapState, mapActions } from "vuex";
export default {
  name: "home",
  data() {
    return {
      scrollTop: 0,
      isModalJoinVoteShow: false,
      isModalNodeDetailShow: false,
      isModalRuleShow: false,
      lastRoundData: {},
      isSelectOpen: false,
      nowRoundData: {},
      loading: false,
      tabChecked: 0,
      nowRoundDataId: "", // 用于select 当前选择的列表ID
      nowRoundDataRound: "", // 用于select 当前选择的列表round
      nowData: null, // 当前操作的data对象
      searchValue: undefined,
      loadingDown: false, // 是否正在下载中
      pdf: {
        en: {
          url: "/work/test/img/pdf_en.pdf",
          name: "DNA Supernode Application.pdf"
        },
        zh: { url: "/work/test/img/pdf_zh.pdf", name: "DNA超级节点申请表.pdf" }
      }
    };
  },
  components: {
    ModalJoinVote,
    ModalNodeDetail,
    ModalRule,
    Loading
  },
  computed: {
    ...mapState({
      roundList: state => state.app.roundList
    }),
    roundListFormat() {
      return this.roundList
        .filter(item => {
          return (
            item.id !==
            (this.lastRoundData.period && this.lastRoundData.period.id)
          );
        })
        .sort((a, b) => b.round - a.round);
    },
    nowDataFormat() {
      const data = this.nowRoundData.periodSelections || [];
      return data
        .sort((a, b) => b.voteCount - a.voteCount)
        .map((item, index) => {
          return { ...item, no: index + 1 };
        });
    },
    showData() {
      if (this.searchValue) {
        return this.nowDataFormat.filter(item => {
          return String(item.selectionName)
            .toUpperCase()
            .includes(String(this.searchValue).toUpperCase());
        });
      }
      return this.nowDataFormat;
    }
  },
  watch: {
    roundListFormat(newV) {
      this.nowRoundDataRound = newV[0] && newV[0].round;
      this.nowRoundDataId = newV[0] && newV[0].id;
    }
  },
  filters: {
    formartLv(v) {
      return String(v).padStart(2, "0");
    },
    formartNum(v) {
      if (!v && v !== 0) {
        return "-";
      }
      return Number(v).toLocaleString("en-US");
    },
    formatDate(v) {
      if (!v) {
        return "-";
      }
      return moment(v).format("YYYY/MM/DD hh:mm");
    },
    formartPercent(v) {
      if (!v) {
        return `0%`;
      }
      return `${Number(v * 100).toFixed(0)}%`;
    }
  },
  async mounted() {
    document.addEventListener("scroll", this.scrollChange, { passive: true });
    this.scrollChange();
    this.lastRoundData = await this.getRoundData();
    this.nowRoundData = this.lastRoundData;
    this.getRoundList();
  },
  beforeDestroy() {
    document.removeEventListener("scroll", this.scrollChange, {
      passive: true
    });
  },
  methods: {
    ...mapActions({
      getRoundList: "app/getRoundList",
      getRoundDataById: "app/getRoundDataById"
    }),
    scrollChange(e) {
      this.$refs.back.style.backgroundPosition = `center -${0.1 *
        window.pageYOffset}px`;
    },
    // 获取某一期的数据
    getRoundData(id) {
      this.loading = true;
      return this.getRoundDataById({ period: id })
        .then(res => {
          if (res && res.code === 0) {
            return res.data;
          }
          return {};
        })
        .catch(() => {
          this.$message.error("网络错误，请重试");
          return {};
        })
        .finally(() => {
          this.loading = false;
        });
    },
    async getData(id) {
      const res = await this.getRoundData(id);
      this.nowRoundData = res;
    },

    /** 异步请求文件数据 **/
    async getFileData() {
      if (this.loadingDown) {
        return;
      }
      const src = this.pdf[this._i18n.locale].url; // 获取链接地址
      const fileName = this.pdf[this._i18n.locale].name; // 获取文件名
      this.loadingDown = true;
      // 让浏览器弹出下载
      await download(src, fileName);
      this.loadingDown = false;
    },

    // 用户点击投票
    onClickVote() {
      this.isModalJoinVoteShow = true;
    },
    // 用户点击查看详情
    onClickDetail(item) {
      this.nowData = item;
      this.isModalNodeDetailShow = true;
    },
    // 用户来回切换期数
    onTabChose(type) {
      if (this.tabChecked === type) {
        return;
      }
      this.tabChecked = type;
      if (type === 0) {
        this.loading = true;

        setTimeout(() => {
          this.nowRoundData = { ...this.lastRoundData };
          this.loading = false;
        }, 300);
      } else if (type === 1) {
        this.getData(this.nowRoundDataId);
      }
    },

    // 用户搜索框输入
    onSearch(v) {
      this.searchValue = v.target.value;
    },

    onSelectRound(v) {
      this.tabChecked = 1;
      this.nowRoundDataId = v.key;
      this.nowRoundDataRound = v.domEvent.target.getAttribute("data-round");
      this.getData(v.key);
    }
  }
};
</script>
