<template>
  <a-modal class="modal-join-vote"
           :visible="isShow"
           :maskClosable="true"
           :closable="false"
           :footer="null"
           style="top: 25vh;"
           @ok="onClose"
           @cancel="onClose">
    <div>
      <div class="title">{{ $t("details") }}</div>
      <div class="info">{{ $t("joinVote1") }}</div>
      <ul class="links">
        <!-- <li>
          <a :href="url[_i18n.locale]" target="_blank" rel="nofollow">
            <div class="icon"><img src="@/assets/img/R_logo.png" alt="RightBTC" /></div>
            <div class="name">RightBTC</div>
          </a>
        </li> -->
        <li>
          <a @click="getFileData('br')"
             target="_blank"
             rel="nofollow">
            <div class="icon"><img src="@/assets/img/BR_logo.png"
                   alt="BR" /></div>
            <div class="name">BR</div>
          </a>
        </li>
        <li>
          <a @click="getFileData('etp')"
             target="_blank"
             rel="nofollow">
            <div class="icon"><img src="@/assets/img/M_logo.png"
                   alt="ETP" /></div>
            <div class="name">{{ $t("myETP") }}</div>
          </a>
        </li>
      </ul>
    </div>
  </a-modal>
</template>
<script>
import axios from "axios";
import { download } from "@/utils/tools";

export default {
  name: "modal-join-vote",
  data() {
    return {
      url: {
        en: "https://rightbtc.zendesk.com/hc/en-us/articles/360037038572",
        zh: "https://rightbtc.zendesk.com/hc/zh-cn/articles/360037038572"
      },
      loadingDownETP: false,
      pdf: {
        br: {
          en: { url: null, name: "#.pdf" },
          zh: { url: null, name: "#.pdf" }
        },
        etp: {
          en: {
            url: "/img/etp_en.pdf",
            name: "DNA voting in MyETPWallet.pdf"
          },
          zh: { url: "/img/etp_cn.pdf", name: "DNA节点投票轻钱包教程.pdf" }
        }
      }
    };
  },
  props: {
    isShow: { type: Boolean, default: false }
  },

  methods: {
    onClose() {
      this.$emit("onClose");
    },
    comingSoon() {
      this.$message.info(this.$t("soon"));
    },
    /** 异步请求文件数据 **/
    async getFileData(type) {
      const src = this.pdf[type][this._i18n.locale].url; // 获取链接地址
      if (!src) {
        this.comingSoon();
        return;
      }
      const fileName = this.pdf[type][this._i18n.locale].name; // 获取文件名

      if (type === "etp") {
        if (this.loadingDownETP) {
          return;
        }
        this.loadingDownETP = true;
      }
      // 让浏览器弹出下载
      await download(src, fileName);
      this.loadingDownETP = false;
    }
  }
};
</script>
