import Vue from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";
import "./assets/css/normalize.css";
import VueI18n from "vue-i18n";
import messages from "./assets/i18n.js";
import { Button, Icon, Input, Modal, message, Popover, Select, BackTop, Dropdown, Menu } from "ant-design-vue";
Vue.use(VueI18n);
Vue.use(Button);
Vue.use(Icon);
Vue.use(Input);
Vue.use(Modal);
Vue.use(Popover);
Vue.use(Select);
Vue.use(BackTop);
Vue.use(Dropdown);
Vue.use(Menu);
message.config({
  maxCount: 3,
  duration: 1,
});
Vue.prototype.$message = message;

Vue.config.productionTip = false;

const lang = localStorage.getItem("lang") || "en";
const i18n = new VueI18n({
  locale: lang,
  messages,
});
new Vue({
  i18n,
  router,
  store,
  render: h => h(App),
}).$mount("#app");
