  <div :class="['modal-rule', { show: isShow }]">
    <div class="jiao">
      <div></div>
    </div>
    <div class="title">{{ $t("rule.t") }}</div>
    <ul class="info-box">
      <li>
        <div>1. {{ $t("rule.t1") }}</div>
        <div>{{ $t("rule.i1") }}</div>
      </li>
      <li>
        <div>2. {{ $t("rule.t2") }}</div>
        <div>{{ $t("rule.i2") }}</div>
      </li>
      <li>
        <div>3. {{ $t("rule.t3") }}</div>
        <div>{{ $t("rule.i3") }}</div>
      </li>
      <li>
        <div>4. {{ $t("rule.t4") }}</div>
        <div>{{ $t("rule.i4") }}</div>
      </li>
      <li>
        <div>5. {{ $t("rule.t5") }}</div>
        <ul>
          <li>
            <span>1)</span><span>{{ $t("rule.i5a") }}<br />{{ $t("rule.i5a1") }}</span>
          </li>
          <li>
            <span>2)</span><span>{{ $t("rule.i5b") }}</span>
          </li>
        </ul>
      </li>
      <li>
        <div>6. {{ $t("rule.t6") }}</div>
      </li>
      <li>
        <div>7. {{ $t("rule.t7") }}</div>
        <ul>
          <li>
            <span>1)</span><span>{{ $t("rule.i7a") }}</span>
          </li>
          <li>
            <span>2)</span><span>{{ $t("rule.i7b") }}</span>
          </li>
        </ul>
      </li>
      <li>
        <div>8. {{ $t("rule.t8") }}</div>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "modal-rule",
  props: {
    isShow: { type: Boolean, default: false },
  },
  watch: {
    isShow(newV) {
      let maskDom = document.getElementById("modal-rule-mask");
      if (newV) {
        maskDom.className = "modal-rule-mask";
        setTimeout(() => {
          maskDom.className = "modal-rule-mask show";
        });
      } else {
        maskDom.className = "modal-rule-mask";
      }
    },
  },
  mounted() {
    let maskDom = document.getElementById("modal-rule-mask");
    if (!maskDom) {
      maskDom = document.createElement("div");
      maskDom.id = "modal-rule-mask";
      document.body.appendChild(maskDom);
      maskDom.addEventListener("click", this.onClose, false);
    }
  },
  beforeDestroy() {
    let maskDom = document.getElementById("modal-rule-mask");
    if (maskDom) {
      maskDom.removeEventListener("click", this.onClose, false);
      document.body.removeChild(maskDom);
    }
  },
  methods: {
    onClose() {
      this.$emit("onClose");
    },
  },
};
</script>
