import server from "@/utils/server";
import Vue from "vue";

const app = {
  namespaced: true,
  state: {
    roundList: [], // 所有的期数
  },
  mutations: {
    setRoundList(state, payload) {
      state.roundList = payload.data;
    },
  },
  actions: {
    // 获取所有的期数
    async getRoundList(context) {
      try {
        const msg = await server.get(`api/dna-selection/v1/periods`);
        if (msg.data.code === 0) {
          // 给msg.data按照日期排序
          context.commit({
            type: "setRoundList",
            data: msg.data.data,
          });
        }
        return msg.data;
      } catch (e) {
        Vue.prototype.$message.info("Network Error");
      }
    },
    // 获取指定期的数据
    async getRoundDataById(context, params) {
      try {
        const res = await server.get(`api/dna-selection/v1/period/info`, params);
        return res.data;
      } catch (e) {
        Vue.prototype.$message.info("Network Error");
      }
    },
  },
};

export default app;
