<script>
export default {
  name: "Header",
  data() {
    return {
      isMobileNavShow: false,
      langName: "English"
    };
  },
  props: {
    msg: String
  },
  mounted() {
    document.addEventListener("scroll", this.onScrollChange, { passive: true });
    this.onScrollChange();
    this.changeLang(this._i18n.locale);
  },
  beforeDestroy() {
    document.removeEventListener("scroll", this.onScrollChange, {
      passive: true
    });
  },

  methods: {
    onScrollChange() {
      this.$refs.header.style.height = `${Math.max(
        45,
        60 - window.pageYOffset / 4
      )}px`;
      this.$refs.header.style.backgroundColor = `rgba(255,255,255,${Math.max(
        0.8,
        1 - window.pageYOffset / 20
      )})`;
    },
    onMobileNavShow() {
      this.isMobileNavShow = !this.isMobileNavShow;
    },
    changeLang(lang) {
      this._i18n.locale = lang;
      let langName = "English";
      switch (lang) {
        case "en":
          langName = "English";
          break;
        case "zh":
          langName = "简体中文";
          break;
      }
      this.langName = langName;
      try {
        localStorage.setItem("lang", lang);
      } catch {}
    }
  }
};
</script>

